@import 'variables.scss';
nav{
    background-color: $primary-color;
    max-height: 10px;
    min-height: 5vh;
    display: flex;
    align-content: center;
    justify-content: center;
    a{
        margin-right: 0.5em;
        margin-left: 0.5em;
        text-decoration: none;
        appearance: none;
        background-color: darken($primary-color, 10%);
        text-align: center;
        line-height: 28px;
        border-radius: 5px;
        transition: 0.3s;
        &:hover{
            background-color: darken($primary-color, 20%);
        }
    }
}